import React from 'react'
import { Link } from 'gatsby'
import cx from 'classnames'

import styles from './MainWidget.module.scss'


export default function MainWidget({ location, info, mobile: isMobile }) {
    const pathname = location ? location.pathname : '/404/'

    return pathname.includes('trade')
        ? (
            <div className={cx([
                styles.widget,
                styles.widget_trade,
                { [styles.widget_mobile]: isMobile },
            ])}
            >
                <div className={styles.info}>
                    <div className={styles.info__text}>Сделки:</div>
                    <div className={styles.info__text}>
                        Исполненно
                        {' '}
                        <Link to="/trade/deals/">
                            {info.orders_count}
                            {' '}
                            сделок
                        </Link>
                    </div>
                </div>
                <div className={styles.info}>
                    <div className={styles.info__text}>
                        Заявки:
                        {isMobile && <br />}
                        {' '}
                        Зарегистрированно
                        {' '}
                        <Link to="/trade/applications/">
                            {info.trades_count}
                            {' '}
                            заявок
                        </Link>
                        <br />
                        <Link to="/trade/applications/">
                            {info.orders_done_count}
                            {' '}
                            исполненно
                        </Link>
                        <hr />
                        <Link to="/trade/applications/">
                            {info.orders_undone_count}
                            {' '}
                            активных
                        </Link>
                    </div>
                </div>
                <div className={styles.info}>
                    <div className={styles.info__text}>
                        Поручения:
                        <br />
                        <Link to="/trade/orders/">Подано 0</Link>
                        <hr />
                        <Link to="/trade/orders/">0 Черновых</Link>
                    </div>
                </div>
            </div>
        )
        : (
            <div className={cx([
                styles.widget,
                { [styles.widget_mobile]: isMobile },
            ])}
            >
                <div className={styles.info}>
                    <div className={styles.info__title}>Активы:</div>
                    <div className={styles.info__text}>
                        1 234 456 руб.
                        <hr />
                        19 041$
                        <hr />
                        56%
                    </div>
                </div>
                <div className={styles.info}>
                    <div className={styles.info__title}>Свободные средства:</div>
                    <div className={styles.info__text}>
                        1 234 456 руб.
                        <hr />
                        19 041$
                        <hr />
                        56%
                    </div>
                </div>
                <div className={styles.info}>
                    <div className={styles.info__title}>Всего:</div>
                    <div className={styles.info__text}>
                        1 234 456 руб.
                        <hr />
                        19 041$
                        <hr />
                        56%
                    </div>
                </div>
            </div>
        )
}
